import React from 'react'
import Message from 'Components/common/message'
import API from 'Lib/api'

export default class Profile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            mobile: '',
        };
        this.api = new API();
    }
    render() {
        return (
            <div className="right d-flex flex-column">
                <div className="content">
                    <div className="title"></div>
                    <form>
                        <div className="form-group row">
                            <label className="col-md-2 col-form-label text-right"></label>
                            <div className="col-md-4">
                                <input ref={username => {this.username = username}} type="text" className="form-control" placeholder="" value={this.state.name} onChange={this.onChangeHandle.bind(this, 'name')}/>
                            </div>
                        </div>
                        <div className="form-group row">
                            <label className="col-md-2 col-form-label text-right"></label>
                            <div className="col-md-6">
                                <input ref={mobile => {this.mobile = mobile}} type="text" className="form-control" placeholder="" value={this.state.mobile} onChange={this.onChangeHandle.bind(this, 'mobile')}/>
                            </div>
                        </div>
                        <div className="form-group row">
                            <label className="col-md-2 col-form-label text-right"></label>
                            <div className="col-md-6">
                                <input ref={password => {this.password = password}} type="password" className="form-control" placeholder=""/>
                            </div>
                        </div>
                        <div className="form-group row">
                            <label className="col-md-2 col-form-label text-right"></label>
                            <div className="col-md-6">
                                <input ref={repassword => {this.repassword = repassword}} type="password" className="form-control" placeholder=""/>
                            </div>
                        </div>
                        <div className="form-group row">
                            <div className="offset-md-2 col-md-4">
                                <button className="btn btn-primary" type="button" onClick={this.onClick.bind(this)}></button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
    componentDidMount() {
        let data= JSON.parse(localStorage.getItem('data'));
        this.state.name = data.profile.name;
        this.state.mobile = data.profile.mobile;
        this.setState(this.state);
    }
    onChangeHandle(type, event) {
        this.state[type] = event.target.value;
        this.setState(this.state);
    }
    onClick() {
        if (this.username.value == '') {
            Message.error({
                content: '',
                duration: 3000
            });
            return;
        }
        if (this.mobile.value == '') {
            Message.error({
                content: '',
                duration: 3000
            });
            return;
        }
        if (/^[1][3,4,5,7,8][0-9]{9}$/.test(this.mobile.value) == false) {
            Message.error({
                content: '',
                duration: 3000
            });
            return;
        }
        if (this.password.value != '') {
            if (this.repassword.value == '') {
                Message.error({
                    content: '',
                    duration: 3000
                });
                return;
            }
            if (this.repassword.value != this.password.value) {
                Message.error({
                    content: '',
                    duration: 3000
                });
                return;
            }
        }
        let data = {
            name: this.username.value,
            mobile: this.mobile.value,
            password: this.password.value
        };

        this.api.post('/profile', data).then(data => {
            if (data.status) {
                Message.success({
                    content: '',
                    duration: 3000
                });
            } else {
                Message.warning({
                    content: '',
                    duration: 3000
                });
            }
        }).catch(error => {
            Message.warning({
                content: '',
                duration: 3000
            });
        });
    }
}